.nav_brand img {
    width: 90px;
}

.nav_sticky {
    /* position: fixed; */
    /* top: 0; */
    background-color: white;
    z-index: 3000;
}

.nav_main {
    padding: 10px 0px;
    height: 95px;
    background-color: white;
}

.nav_links a {
    text-decoration: none;
    color: black;
    margin: 0px 30px;
    font-size: 17px;
    font-weight: 400;
}

#nav_contact {
    color: var(--blue-btn);
    border: 2px solid var(--blue-btn);
    padding: 8px 25px;
    border-radius: 8px;
    text-transform: uppercase;
    font-weight: 500;
    transition: .2s ease;
}

#nav_contact:hover {
    background-color: var(--blue-btn);
    color: white;
}

.nav_hamburger {
    display: none;
}

.phone_nav {
    background-color: var(--custom-yellow);
    padding: 10px 0px;
    position: fixed;
    width: 100%;
    top: 80px;
    z-index: 3000;
}

.off {
    display: none !important;
}

.phone_nav a {
    text-decoration: none;
    color: black;
    font-size: 16px;
    font-weight: 500;
    margin: 15px 0px;
}

#phone_nav_contact {
    border: 2px solid var(--blue-btn);
    border-radius: 8px;
    padding: 5px 15px;
    background-color: var(--blue-btn);
    color: white;
}

@media (max-width: 768px) {
    .nav_links {
        display: none !important;
    }

    .nav_brand img {
        width: 60px;
    }

    .nav_hamburger {
        display: block;
    }
}

@media (max-width: 426px) {
    .nav_main {
        height: 80px;
    }
}