.contact_form {
    color: white;
    width: 100%;
    border-radius: 10px;
    padding: 35px 30px;
}

.white {
    background-color: white;
    color: black;
}

.coloured {
    background-color: rgba(103, 153, 186, 0.48);

}

.contact_form p {
    margin: 10px 0px;
}

.coloured input,
.coloured textarea {
    background-color: #5E6C84;
    border: none;
}

.white input,
.white textarea {
    background-color: rgb(239, 239, 239);
    border: none;
}

.contact_form button {
    margin: 30px 0px;
    width: 140px;
    height: 35px;
    background-color: var(--blue-btn);
    color: white;
    border: none;
    border-radius: 8px;
    outline: none;
}