.what_we_do_section {
    background-color: var(--custom-grey);
}

.service_card {
    width: 350px;
    padding: 25px 20px;
    background-color: white;
    border-radius: 10px;
    margin: 20px 30px;
}

.service_card img {
    width: 100%;
    height: 150px;
    object-fit: cover;
    margin: 10px 0px;
}

.service_heading h3 {
    font-size: 22px;
}

.service_heading img {
    width: 25px;
    height: 25px;
    margin-right: 5px;
}

@media (max-width: 426px) {
    .service_card {
        margin: 10px 10px;
    }
}