.hero {
    /* margin-top: 95px; */
}

.custom-slide img {
    position: relative;
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    height: auto;
    object-fit: cover;
    text-align: center;

    /* image size 1445 X 680 */
}

.texts {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
}

.texts .heading {
    font-size: 45px;
    font-weight: bold;
    text-align: center;
    margin: 15px 0px;
}

.texts .brief {
    text-align: center;
}

/* experience section */

.experience_section {
    min-height: 100vh;
    background-color: var(--custom-grey);
    padding: 140px 0px;
}

.exp_box {
    width: 50%;
    padding: 10px 20px;
}

.exp_box h1 {
    margin: 15px 0px;
    font-weight: bold;
    font-size: 55px;
}

.exp_box p {
    margin: 40px 0px;
}

/* clients section */

.clients {
    padding: 50px 0px;
}

.clients img {
    height: 100px;
    width: 250px;
}


/* expertise section */

.expertise_section {
    background-color: var(--custom-yellow);
}

/* special_service_section */

.special_service_section {
    /* height: 100vh; */
    background-image: url(../assets/special_service.png);
    background-repeat: no-repeat;
    object-fit: cover;
    width: 100%;
    background-position: center;
    background-size: cover;
}

.boxes {
    margin: 0;
    padding: 70px 0px;
    width: 50%;
}

.boxes h1 {
    font-size: 45px;
    text-align: center;
    margin: 30px 0px;
    margin-bottom: 50px;
}

/* services section */

.service_section {
    background-color: var(--custom-grey);
    padding: 120px 0px;
}

.service_cards {
    margin: 20px 0px;
}

.services_heading h1 {
    font-size: 45px;
    margin: 20px 0px;
    font-weight: bold;
}

.services_heading img {
    width: 180px;
    height: auto;
}

/* mission section */

.mission_section {
    background-color: white;
}

/* why us section */

.why_us_section {
    background-color: var(--custom-yellow);
}

.why_us_section p {
    margin: 15px 0px;
    padding: 0;
}

/* .why_us.second img {
    padding: 15px 10px;
} */

.bullet_icon {
    position: relative;
}

.bullet_number {
    font-weight: bold;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.point_name {
    font-weight: bold;
    font-size: 20px;
    margin: 0px 5px;
}

.point_body {
    width: 350px;
    margin: 2px 0px;
}

/* special security section */

.special_security {
    background-image: url(../assets/special_security.png);
}

/* contact section */

.contact_us_section {
    background-image: url(../assets/contact_image.svg);
    padding: 120px 0px;
}

.contact_info {
    color: white;
}

.address {
    font-size: 18px;
}

.contact_info h1 {
    color: white;
    font-weight: bold;
}

.phone_numbers p {
    margin: 1px;
}

.contact_info img {
    margin-left: -10px;
    width: 60px;
    height: auto;
}

.images img {
    width: 490px;
    height: auto;
    object-fit: cover;
}

@media (max-width: 768px) {
    .texts .heading {
        font-size: 20px;
    }

    .point_body {
        width: 100%;
    }

    .exp_box h1 {
        font-size: 35px;
    }

    .special_service_section h1 {
        font-size: 35px;
    }

    .special_service_section img {
        width: 200px;
    }

    .services_heading h1 {
        font-size: 35px;
    }

    .texts .brief {
        font-size: 14px;
    }
}

@media (max-width: 426px) {
    .point_body {
        width: 270px;
        margin: 2px 0px;
    }

    .contact_us_section {
        padding: 40px 0px;
    }

    .point_name {
        font-size: 15px;
        margin: 0px 5px;
    }

    .bullet_number {
        font-size: 15px;
    }

    .services_heading h1 {
        font-size: 25px;
    }

    .service_section {
        background-color: var(--custom-grey);
        padding: 60px 0px;
    }

    .service_cards {
        margin: 0;
    }

    .services_heading img {
        width: 120px;
        height: auto;
    }

    .clients {
        padding: 10px 0px;
    }

    /* .clients img {
        height: 20px;
        width: 20px;
        margin: 10px;
    } */

    .texts .heading {
        font-size: 12px;
        margin: 5px 0px;
    }

    .boxes {
        margin: 0;
        padding: 50px 0px;
        width: 100%;
    }

    .boxes img {
        width: 200px;
        height: auto;
    }

    .boxes h1 {
        font-size: 25px;
    }

    .second img {
        margin: 30px 0px;
    }

    .texts .brief {
        font-size: 10px;
    }

    .exp_box h1 {
        font-size: 20px;
    }

    .exp_box p {
        margin: 10px 0px;
    }

    .experience_section {
        padding: 30px 0px;
    }

    .exp_box {
        width: 100%;
    }

    .hero {
        /* margin-top: 80px; */
    }
}