.custom_button a {
    text-decoration: none;
    text-transform: uppercase;
    color: white;
    border: 2px solid var(--blue-btn);
    margin: 10px 0px;
    padding: 8px 25px;
    transition: .2s;
    border-radius: 8px;
    font-weight: 500;
    background-color: var(--blue-btn);
}

@media (max-width: 426px) {
    .custom_button a {
        padding: 6px 20px;
        font-size: 14px;
    }
}