.about_box h1 {
    font-weight: bold;
}

.backed_image_container {
    position: relative;
}

.first_image {
    height: auto;
}

.page_image img {
    width: 480px !important;
    height: auto !important;
    object-fit: cover !important;
}

.second_image {
    position: absolute;
    background-color: yellow;
    height: 120%;
    width: 40%;
    top: 50%;
    /* right: -30%; */
    /* left: 10%; */
    z-index: -100;
    transform: translate(-50%, -50%);
}

@media (max-width: 1440px) {
    .first_image {
        width: 500px;
    }
}

@media (max-width: 1024px) {
    .first_image {
        width: 300px;
    }
}

@media (max-width: 376px) {
    .first_image {
        width: 240px;
    }
}