.card_body {
    width: 360px;
    height: 460px;
    background-color: white;
    border-radius: 8px;
    padding: 10px 15px;
    margin: 20px 10px;
}

.card_body h2 {
    font-size: 26px;
    margin: 10px 0px;
}

/* .card_holder {
    width: 50% !important;
} */

.swiper {
    width: 100%;
    padding-top: 50px;
    padding-bottom: 50px;
}

.swiper-slide {
    background-position: center;
    background-size: cover;
    width: 300px;
    height: 300px;
}

.swiper-slide img {
    display: block;
    width: 100%;
}

.card_body p {
    margin-bottom: 35px;
    height: 100px;
}

.card_body img {
    width: 100%;
    height: 200px;
    object-fit: cover;
}

@media (max-width: 426px) {
    .card_body h2 {
        font-size: 22px;
    }
}

@media (max-width: 320px) {
    .card_body {
        width: 300px;
        margin-left: 0;
        margin-right: 0;
    }
}