.page_hero img {
    width: 100%;
    object-fit: cover;
    height: 380px;
}

.page_hero {
    position: relative;
}

.page_hero_text {
    color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.page_hero_text h1 {
    font-size: 45px;
    font-weight: bold;
    margin: 20px 0px;
    text-align: center;
}

.page_hero_text p {
    text-align: center;
}

.middle_logo {
    padding: 40px 0px;
}

.middle_logo img {
    width: 250px;
    height: 251px;
}

@media (max-width: 426px) {
    .middle_logo {
        padding: 10px 0px;
    }

    .middle_logo img {
        width: 150px;
        height: 151px;
    }

    .page_hero img {
        height: 200px;
    }

    .page_hero_text h1 {
        font-size: 25px;
        margin: 5px 0px;
    }

    .page_hero_text p {
        font-size: 12px;
    }
}