.contact_page_info {
    background-color: var(--blue-btn);
    padding: 40px 0px;
    color: white;
}

.contact_page_heading h1 {
    font-weight: bold;
    margin: 20px 0px;
    text-align: center;
}

.contact_page_info_box div {
    padding: 40px;
}

.contact_page_info_box p {
    margin: 0;
    text-align: center;
}

.contact_contact_box {
    padding: 40px 0px;
    background-color: var(--custom-yellow);
}

.contact_page_left h3 {
    margin: 30px 0px;
}

.contact_page_left {
    text-align: center;
}

.contact_form_section {
    padding: 60px 0px;
}

.contact_contact_box img {
    width: 200px;
}

@media (max-width: 426px) {
    .contact_page_info_box div {
        padding: 10px;
    }

    .contact_form_section {
        padding: 20px 0px;
    }
}