.footer_main {
    background-color: #002F39;
    color: white;
    padding: 100px 0px;
}

.footer_1st img {
    width: 150px;
    height: auto;
    margin-bottom: 20px;
}

.company_desc {
    width: 80%;
}

.footer_2nd a {
    text-decoration: none;
    color: white;
    margin: 5px 0px;
}

.copyright {
    color: #CAC4D0;
}

.copyright_holder p {
    text-align: center;
}

.footer_last input {
    width: 300px;
    padding-left: 10px;
    outline: none;
    height: 40px;
    border: none;
    background-color: white;
    border-radius: 5px;
}

.icons img {
    margin: 10px 15px;
}

.icons img:first-child {
    margin-left: 0;
}

.subscription_form button {
    height: 40px;
    border-radius: 5px;
    background-color: var(--blue-btn);
    color: white;
    width: 150px;
    margin: 20px 0px;
    border: none;
}

.credit {
    background-color: #17191C;
    color: white;
    height: 60px;
}

.credit a {
    text-decoration: none;
    color: rgb(175, 175, 255);
}

@media (max-width: 1000px) {
    .footer_last input {
        width: 100%;
    }
}

@media (max-width:426px) {
    .footer_main {
        padding: 50px 0px;
    }

    .company_desc {
        width: 300px;
    }
}